body {
    margin: 0;
    height: 100vh;
    font-family: 'Roboto', sans-serif;
}

#root {
    height: 100%;
}

.app .btn-toggle {
    float: right;
    cursor: pointer;
    width: 45px;
    height: 45px;
    color: #000;
    text-align: center;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    display: none;
    z-index: 9;
   
}

.app .sidebar-btn-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

.app {
    height: 100%;
    display: flex;
    position: relative;
}

.app .sidebar-btn-wrapper .sidebar-btn {
    transition: width 0.3s;
    width: 150px;
    padding: 1px 15px;
    border-radius: 40px;
    background: rgba(255, 255, 255, 0.05);
    color: #adadad;
    text-decoration: none;
    margin: 0 auto;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-overflow: ellipsis;
    overflow: hidden;
}

.app .sidebar-btn-wrapper .sidebar-btn span {
    margin-left: 5px;
    font-size: 13px;
}

.app .sidebar-btn-wrapper .sidebar-btn:hover {
    color: red;
}

.app .collapsed .sidebar-btn {
    display: inline-block;
    line-height: 40px;
    padding: 0;
}

.app main {
    flex-grow: 1;
    flex-direction: column;
    overflow-y: auto;
    position: relative;
}

.app main header {
    margin-bottom: 10px;
    border-bottom: 1px solid #efefef;
}

.app main header h1 {
    display: flex;
    align-items: center;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
}

.app main footer {
    margin-top: auto;
    color: #888;
    text-align: center;
}

.app main footer a {
    text-decoration: none;
    color: #888;
}

.app main footer a:hover {
    text-decoration: underline;
}

.app .social-bagdes {
    margin-top: 10px;
}

.app .social-bagdes img {
    margin: 5px;
}

.app .block {
    display: flex;
    margin-bottom: 24px;
    font-size: 14px;
    color: #545454;
}

.app .block>span {
    margin-left: 10px;
}

.app.rtl {
    direction: rtl;
    text-align: right;
}

.app.rtl header h1 {
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}

.app.rtl .block {
    direction: rtl;
}

.app.rtl .block>span {
    margin-left: 0;
    margin-right: 10px;
}

.app.rtl .sidebar-btn-wrapper .sidebar-btn span {
    margin-left: 0;
    margin-right: 5px;
}

@media (max-width: 767.98px) {
    .app {
        overflow-x: hidden;
    }
    .app header h1 {
        font-size: 24px;
    }
}

@media (max-width: 767.98px) {
    .app .btn-toggle {
        display: flex;
        z-index: 9;
        float: right;
    }
    .app.rtl .btn-toggle {
        margin-left: auto;
    }
}

.badge {
    padding: 3px 6px;
    font-size: 9px;
    letter-spacing: 1px;
    border-radius: 15px;
}

.badge.red {
    color: #fff;
    background: #d63030;
}

.badge.gray {
    color: #fff;
    background: #777;
}

.badge.yellow {
    color: #000;
    background: #ffd331;
}

.sidebar_logo {
    width: -webkit-fill-available;
}

.pro-sidebar .pro-menu .pro-menu-item>.pro-inner-item:hover {
    color: #07299b;
}

.pro-sidebar {
    color: #adadad;
    height: 100%;
    width: 245px;
    min-width: 245px;
    text-align: left;
    transition: width, left, right, 0.3s;
    position: relative;
    z-index: 1009;
}

